.list-section .list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e2e8f0;
}
.list-section .list-header .orders-count {
  font-size: 1rem;
  font-weight: 500;
  color: #374151;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.list-section .list-header .orders-count .selected-count {
  font-size: 0.875rem;
  color: #2563eb;
  font-weight: 600;
}
.list-section .list-header .bulk-operations {
