  width: 1rem;
  height: 1rem;
  accent-color: #2563eb;
}
.list-section .list-header .bulk-operations .bulk-selection .select-all-label .select-all-text {
  font-weight: 500;
}
.list-section .list-header .bulk-operations .bulk-actions {
