  gap: 1rem;
  align-items: center;
  flex-wrap: wrap;
}
.list-section .list-header .bulk-operations .bulk-actions .bulk-action-group {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.list-section .list-header .bulk-operations .bulk-actions .bulk-action-group .bulk-action-btn {
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
