}
.list-section .list-header .bulk-operations .bulk-actions .bulk-action-group .bulk-action-btn svg {
  width: 1rem;
  height: 1rem;
}
.list-section .list-header .bulk-operations .bulk-actions .bulk-delete-btn {
  padding: 0.5rem 0.75rem;
  border: 1px solid #ef4444;
  border-radius: 6px;
  background: white;
  color: #ef4444;
  font-size: 0.75rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  white-space: nowrap;
}
.list-section .list-header .bulk-operations .bulk-actions .bulk-delete-btn:hover {
  background: #ef4444;
  color: white;
}
.list-section .list-header .bulk-operations .bulk-actions .bulk-delete-btn svg {
  width: 1rem;
  height: 1rem;
}
.list-section .list-header .header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.list-section .list-header .header-actions .bulk-delete-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  padding: 0 12px;
  border-radius: 10px;
  background: #fef2f2;
