}
.list-section .list-header .header-actions .bulk-delete-btn svg {
  width: 16px;
  height: 16px;
}
.list-section .list-header .header-actions .bulk-mode-btn {
  background: none;
  border: 1px solid #d1d5db;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #6b7280;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
}
.list-section .list-header .header-actions .bulk-mode-btn:hover {
  border-color: #9ca3af;
  background: #f9fafb;
  color: #374151;
}
.list-section .list-header .header-actions .bulk-mode-btn.active {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}
.list-section .list-header .header-actions .bulk-mode-btn.active:hover {
  background: #1d4ed8;
}
.list-section .list-header .header-actions .bulk-mode-btn svg {
  width: 1rem;
  height: 1rem;
}
.list-section .list-header .header-actions .refresh-btn {
  background: none;
  border: 1px solid #d1d5db;
  padding: 0.5rem;
