  transition: all 0.2s ease;
  color: #6b7280;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-section .list-header .header-actions .refresh-btn:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
  color: #374151;
}
.list-section .list-header .header-actions .refresh-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.list-section .list-header .header-actions .refresh-btn svg {
  width: 1rem;
  height: 1rem;
}
.list-section .list-header .header-actions .view-toggle {
  display: flex;
  gap: 0.25rem;
  background: #f3f4f6;
  border-radius: 8px;
  padding: 0.25rem;
}
.list-section .list-header .header-actions .view-toggle .view-btn {
  background: none;
  border: none;
  padding: 0.5rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #6b7280;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-section .list-header .header-actions .view-toggle .view-btn:hover {
  color: #374151;
  background: #e5e7eb;
}
.list-section .list-header .header-actions .view-toggle .view-btn.active {
  background: white;
  color: #2563eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.list-section .list-header .header-actions .view-toggle .view-btn svg {
  width: 1rem;
  height: 1rem;
}
.list-section .loading-state {
  background: white;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e2e8f0;
  padding: 1rem;
}
.list-section .loading-state .loading-skeleton .skeleton-header {
  height: 60px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 8px;
  margin-bottom: 1rem;
}
.list-section .loading-state .loading-skeleton .skeleton-filters {
  height: 50px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 8px;
  margin-bottom: 1.5rem;
}
.list-section .loading-state .loading-skeleton .skeleton-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
}
.list-section .loading-state .loading-skeleton .skeleton-cards .skeleton-card {
  height: 200px;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 12px;
}
@keyframes loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}
.list-section .empty-state {
  background: white;
  border-radius: 12px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e2e8f0;
  padding: 3rem 2rem;
  text-align: center;
}
.list-section .empty-state .empty-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  max-width: 480px;
  margin: 0 auto;
}
.list-section .empty-state .empty-content .empty-icon {
  color: #9ca3af;
  margin-bottom: 0.5rem;
}
.list-section .empty-state .empty-content .empty-icon svg {
  width: 5rem;
  height: 5rem;
}
.list-section .empty-state .empty-content .empty-text .empty-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 0.75rem 0;
  line-height: 1.4;
}
.list-section .empty-state .empty-content .empty-text .empty-description {
  font-size: 1rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.6;
  max-width: 400px;
}
.list-section .empty-state .empty-content .empty-actions {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}
.list-section .empty-state .empty-content .empty-actions .btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s ease;
  border: none;
  cursor: pointer;
  min-width: 140px;
}
.list-section .empty-state .empty-content .empty-actions .btn svg {
  width: 1.25rem;
  height: 1.25rem;
}
.list-section .empty-state .empty-content .empty-actions .btn.btn-primary {
  background: #2563eb;
  color: white;
  box-shadow: 0 1px 3px 0 rgba(37, 99, 235, 0.3);
}
.list-section .empty-state .empty-content .empty-actions .btn.btn-primary:hover {
  background: #1d4ed8;
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(37, 99, 235, 0.4);
}
.list-section .empty-state .empty-content .empty-actions .btn.btn-primary:active {
  transform: translateY(0);
}
.list-section .empty-state .empty-content .empty-actions .btn.btn-secondary {
  background: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.list-section .empty-state .empty-content .empty-actions .btn.btn-secondary:hover {
  background: #f9fafb;
  border-color: #9ca3af;
  transform: translateY(-1px);
}
.list-section .empty-state .empty-content .empty-actions .btn.btn-secondary:active {
  transform: translateY(0);
}
.list-section .work-orders-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.list-section .work-orders-grid .work-order-card-item {
  transition: transform 0.2s ease;
}
.list-section .work-orders-grid .work-order-card-item:hover {
  transform: translateY(-2px);
}
.list-section .work-orders-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}
.list-section .work-orders-list .work-order-card-item.list-view {
