.list-section .work-orders-list .list-table-body .list-table-row .list-table-cell .location-info svg,
.list-section .work-orders-list .list-table-body .list-table-row .list-table-cell .due-date-info svg,
.list-section .work-orders-list .list-table-body .list-table-row .list-table-cell .created-info svg {
