.list-section .work-orders-list .list-table-body .list-table-row .list-table-cell .actions-container .action-btn {
  padding: 0.5rem;
  border: none;
  background: none;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #6b7280;
}
.list-section .work-orders-list .list-table-body .list-table-row .list-table-cell .actions-container .action-btn:hover {
