}
.list-section .pagination-container {
  background: white;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e2e8f0;
  padding: 1rem;
  margin-top: 1rem;
}
.list-section .pagination-container .pagination-info {
  text-align: center;
  color: #6b7280;
  font-size: 0.875rem;
  margin-bottom: 1rem;
}
.list-section .pagination-container .pagination-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
.list-section .pagination-container .pagination-controls .pagination-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border: 1px solid #d1d5db;
  background: white;
  color: #374151;
  border-radius: 6px;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
}
.list-section .pagination-container .pagination-controls .pagination-btn:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
}
.list-section .pagination-container .pagination-controls .pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.list-section .pagination-container .pagination-controls .pagination-btn svg {
  width: 1rem;
  height: 1rem;
}
.list-section .pagination-container .pagination-controls .page-numbers {
  display: flex;
  gap: 0.25rem;
}
.list-section .pagination-container .pagination-controls .page-numbers .page-number {
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
  background: white;
  color: #374151;
  border-radius: 6px;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 2.5rem;
  text-align: center;
}
.list-section .pagination-container .pagination-controls .page-numbers .page-number:hover {
