  border-color: #9ca3af;
}
.list-section .pagination-container .pagination-controls .page-numbers .page-number.active {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}

@media (max-width: 768px) {
  .list-section .list-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
    padding: 1rem;
  }
  .list-section .list-header .header-actions {
    align-self: center;
    flex-direction: column;
    gap: 0.75rem;
  }
  .list-section .work-orders-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .list-section .pagination-container .pagination-controls {
    flex-direction: column;
    gap: 1rem;
  }
  .list-section .pagination-container .pagination-controls .page-numbers {
    order: -1;
  }
  .list-section .empty-state {
    padding: 2rem 1rem;
  }
  .list-section .empty-state .empty-content {
    gap: 1.25rem;
  }
  .list-section .empty-state .empty-content .empty-icon svg {
    width: 4rem;
    height: 4rem;
  }
  .list-section .empty-state .empty-content .empty-text .empty-title {
    font-size: 1.25rem;
  }
  .list-section .empty-state .empty-content .empty-text .empty-description {
    font-size: 0.875rem;
  }
  .list-section .empty-state .empty-content .empty-actions {
    flex-direction: column;
    gap: 0.75rem;
  }
  .list-section .empty-state .empty-content .empty-actions .btn {
    min-width: 200px;
  }
  .list-section .work-orders-list .list-table-header .list-table-row,
  .list-section .work-orders-list .list-table-body .list-table-row {
    grid-template-columns: 1fr;
    gap: 0.5rem;
    padding: 0.75rem;
  }
  .list-section .work-orders-list .list-table-header .list-table-row .list-table-cell,
  .list-section .work-orders-list .list-table-body .list-table-row .list-table-cell {
    justify-content: space-between;
